import React, { Component } from 'react'
import { connect } from 'react-redux'
import Layout from './layout'
import { updateCurrentData } from '../../actions'

class Home extends Component {
    componentDidMount() {
        document.title = "Home"
        this.props.updateCurrentData(this.props.userData.token)
    }
    render() {
        return (
            <Layout
                userData={this.props.userData.user}
            />
        )
    }
}
const mapStateToProps = ({ userData }) => ({
    userData: userData
})
export default connect(mapStateToProps, { updateCurrentData })(Home)