import React, { Component } from 'react'
import { connect } from 'react-redux'
import Layout from './layout'
import { notification } from '../../modules/notification'
import { formDataToObject } from '../../modules/form-validator'
import { API_URL } from '../../config'
import Activities from './activities'
import socketIO from 'socket.io-client'

class Activity extends Component {
    state = {
        buttonDisabled: true,
        activities: []
    }
    onChangeInput = element => {
        let value = element.target.value.trim()
        if (value.length > 0) this.setState({ ...this.state, buttonDisabled: false })
        else this.setState({ ...this.state, buttonDisabled: true })
    }
    getActivities = () => {
        fetch(`${API_URL}/activity/friend-activities/${this.props.userData.user._id}/${this.props.userData.token}`)
            .then(data => data.json())
            .then(data => {
                if (data.status === 200) {
                    this.setState({ ...this.state, activities: data.activities })
                }
            })
    }
    submit = event => {
        event.preventDefault()
        const form = new FormData(event.target)
        form.append('user', this.props.userData.user._id)
        const object = formDataToObject(form)
        if (object.text.length) {
            fetch(`${API_URL}/activity/${this.props.userData.token}`, { method: 'POST', body: form })
                .then(data => data.json())
                .then(data => {
                    notification({
                        text: data.message
                    })
                    if (data.status === 200) {
                        this.setState({ ...this.state, buttonDisabled: true })
                        event.target.reset()
                    }
                })
        }
    }
    slideToggle = (selector) => {
        const element = document.querySelector(selector)
        if (!element.classList.contains('active')) {
            element.classList.add('active')
            element.style.height = 'auto'
            const height = `${element.clientHeight}px`
            element.style.height = `0px`
            setTimeout(() => {
                element.style.height = `${height}`
            }, 0)
        } else {
            element.style.height = '0px'
            element.addEventListener('transitionend', () => {
                element.classList.remove('active')
            }, {
                once: true
            })
        }
    }
    componentDidMount() {
        const socket = socketIO(API_URL, { transports: ["websocket"] })
        socket.emit('join-global-activities')
        socket.on('new-activity', data => {
            this.setState({ ...this.state, activities: [...this.state.activities, data] })
        })
        this.getActivities()
    }
    render() {
        return (
            <Layout
                buttonDisabled={this.state.buttonDisabled}
                onChangeInput={this.onChangeInput}
                submit={this.submit}
                slideToggle={this.slideToggle}
                list={this.state.activities}
                Activities={Activities}
            />
        )
    }
}
const mapStateToProps = ({ userData }) => ({
    userData: userData
})
export default connect(mapStateToProps)(Activity)