import { createElement, append } from './helpers'
import { themes, validateOptions } from './config'

const createnotificationContainer = () => {
    let notificationContainer = document.getElementById('notification-container')
    if (!notificationContainer) {
        notificationContainer = createElement({
            id: 'notification-container',
            type: 'div',
            classList: ['notification-container']
        })
    }
    return notificationContainer
}
const createProgressBar = (duration, theme) => {
    const progressBar = createElement({
        type: 'div',
        classList: ['progress-bar', theme.className]
    })
    setTimeout(() => {
        progressBar.animate([
            { boxShadow: `inset 0px 0 2px 0 ${theme.color}` },
            { boxShadow: `inset ${progressBar.clientWidth}px 0 2px 0 ${theme.color}` }
        ], {
            duration: duration
        })
    }, 150)
    return progressBar
}
const createnotificationIcon = (iconName, theme) => {
    const notificationIcon = createElement({
        type: 'div',
        classList: ['notification-icon']
    })
    const icon = createElement({
        type: 'i'
    })
    icon.className = iconName
    icon.style.color = theme.color
    append(notificationIcon, icon)
    return notificationIcon
}
const createnotificationBody = text => {
    const notificationBody = createElement({
        type: 'div',
        classList: ['notification-body']
    })
    const notificationText = createElement({
        type: 'p',
        innerText: text
    })
    append(notificationBody, notificationText)
    return notificationBody
}
const closeNotification = notification => {
    notification.animate([
        { transform: 'translate(0%, 0%)', opacity: 1 },
        { transform: 'translate(-50%, 50%)', opacity: 0 }
    ], {
        duration: 150
    })
    setTimeout(() => {
        notification.remove()
    }, 150)
}
const notification = options => {
    const notificationOptions = validateOptions(options)
    const notificationContainer = createnotificationContainer()
    const notification = createElement({
        type: 'div',
        classList: ['notification']
    })
    const notificationProgressBar = createProgressBar(notificationOptions.duration, themes[notificationOptions.theme])
    const row = createElement({
        type: 'div',
        classList: ['notification-row']
    })
    if (notificationOptions.displayIcon) {
        const notificationIcon = createnotificationIcon(notificationOptions.icon, themes[notificationOptions.theme])
        append(row, notificationIcon)
    }
    if (notificationOptions.closeOnClick) {
        notification.onclick = () => closeNotification(notification)
    }
    const notificationBody = createnotificationBody(notificationOptions.text)
    append(row, notificationBody)
    append(notification, notificationProgressBar, row)
    append(notificationContainer, notification)
    append(document.body, notificationContainer)
    notification.animate([
        { transform: 'translateY(50%)', opacity: 0 },
        { transform: 'translateY(-15%)', opacity: 1 },
        { transform: 'translateY(15%)', opacity: 1 },
        { transform: 'translateY(0%)', opacity: 1 }
    ], {
        duration: 150
    })
    setTimeout(() => {
        notification.animate([
            { transform: 'translate(0%, 0%)', opacity: 1 },
            { transform: 'translate(-50%, 50%)', opacity: 0 }
        ], {
            duration: 100
        })
    }, notificationOptions.duration)
    setTimeout(() => {
        notification.remove()
    }, notificationOptions.duration + 100)
}
export default notification