import React, { Component } from 'react'
import Layout from './layout'
import { validation, formDataToObject } from '../../modules/form-validator'
import { notification } from '../../modules/notification'
import { API_URL } from '../../config'
class Register extends Component {
    submit = event => {
        event.preventDefault()
        const form = new FormData(event.target)
        const formObject = formDataToObject(form)
        const errors = {
            firstName: 'Write your name.',
            lastName: 'Write your last name.',
            username: 'Write your username.',
            email: 'Write your email.',
            password: 'Write your password.',
            confirmPassword: 'Please confirm your password.'
        }
        const fields = [
            { field: 'firstName', condition: 'isEmpty' },
            { field: 'lastName', condition: 'isEmpty' },
            { field: 'username', condition: 'isEmpty' },
            { field: 'email', condition: 'isEmail' },
            { field: 'password', condition: 'isLength', length: { min: 5 } },
            { field: 'confirmPassword', condition: 'isLength', length: { min: 5 } }
        ]
        const validationResult = validation(formObject, fields)
        if (formObject.password === formObject.confirmPassword && validationResult.length === 0) {
            fetch(`${API_URL}/user`, {
                method: 'POST',
                body: form
            })
            .then(data => data.json())
            .then(data => {
                notification({
                    text: data.message,
                    duration: 5000,
                    theme: data.status === 200 ? 'success' : 'warn'
                })
            })
        } else if (formObject.password !== formObject.confirmPassword) {
            notification({
                text: "Passwords don't match.",
                duration: 7000,
                theme: 'warn'
            })
        } else {
            console.log(validationResult)
            notification({
                text: errors[validationResult[0].field],
                duration: 7000,
                theme: 'warn'
            })
        }
    }
    render() {
        return (
            <Layout
                submit={this.submit}
            />
        )
    }
}
export default Register