import { LOGIN, LOGOUT, UPDATE_CURRENT_DATA } from './types'
import { dataRequested, dataFailure, dataSuccess } from './async-states'
import { API_URL } from '../config'

export const login = (body, callBack) => dispatch => {
    dispatch(dataRequested())
    fetch(`${API_URL}/user/auth`, { method: 'POST', body: body })
        .then(json => json.json())
        .then(data => {
            if (data.status === 200) dispatch(dataSuccess(LOGIN, data.data))
            callBack(data)
        })
        .catch(error => dispatch(dataFailure(error)))
}
export const updateCurrentData = (token) => dispatch => {
    dispatch(dataRequested())
    fetch(`${API_URL}/user/get-data/${token}`, { method: 'GET' })
        .then(json => json.json())
        .then(data => {
            if (data.status === 200) dispatch(dataSuccess(UPDATE_CURRENT_DATA, data.data))
        })
        .catch(error => dispatch(dataFailure(error)))
}
export const logout = () => ({
    type: LOGOUT,
    playload: {}
})