import React, { Component } from 'react'
import { connect } from 'react-redux'
import { validation, formDataToObject } from '../../modules/form-validator'
import Layout from './layout'
import { login } from '../../actions'
import { notification } from '../../modules/notification'

class Login extends Component {
    submit = event => {
        event.preventDefault()
        const form = new FormData(event.target)
        const errors = {
            username: 'Complete username field.',
            password: 'Complete password field.',
        }
        const fields = [
            { field: 'username', condition: 'isEmpty'},
            { field: 'password', condition: 'isEmpty' }
        ]
        const validationResult = validation(formDataToObject(form), fields)
        if (!validationResult.length) {
            this.props.login(form, data => {
                notification({
                    text: data.message,
                    theme: data.status === 200 ? 'success' : 'warn'
                })
                if (data.status === 200) window.location.href = "/home"
            })
        } else if (validationResult.length === fields.length) {
            notification({ text: 'Please, complete the form.', showDuration: 6000, theme: 'danger' })
        } else {
            notification({ text: errors[validationResult[0].field], showDuration: 6000, theme: 'warn' })
        }
    }
    render() {
        return (
            <Layout
                submit={this.submit}
            />
        )
    }
}
export default connect(null, { login })(Login)