class Field {
    constructor(field) {
        this.field = field.trim()
        this.result = {
            correct: false,
            message: ''
        }
    }
    isEmail() {
        const REGEXP = /^(([^<>()\[\]\\.,;:\s@”]+(\.[^<>()\[\]\\.,;:\s@”]+)*)|(“.+”))@((\[[0–9]{1,3}\.[0–9]{1,3}\.[0–9]{1,3}\.[0–9]{1,3}])|(([a-zA-Z\-0–9]+\.)+[a-zA-Z]{2,}))$/
        return REGEXP.test(this.field) ? {
            ...this.result, correct: true
        } : {
            ...this.result, message: 'Email is incorrect, please write it again.'
        }
    }
    isLength({ min = null, max = null }) {
        if (min !== null) return this.field.length < min ? {
            ...this.result, message: 'The content of the field is very short, please complete it correctly.'
        } : {
            ...this.result, correct: true
        }
        if (max !== null) return this.field.length > max ? {
            ...this.result, message: 'The content of the field is long, please complete it correctly.'
        } : {
            ...this.result, correct: true
        }

    }
    isEmpty() {
        return this.field.length === 0 ? {
            ...this.result, message: 'Field empty, fill it.'
        } : {
            ...this.result, correct: true
        }
    }
}
export default Field