import Field from './field'

const identifer = (data, field) => {
    switch (field.condition) {
        case "isEmail":
            return new Field(data[field.field]).isEmail()
        case "isLength":
            return new Field(data[field.field]).isLength(field.length)
        case "isEmpty":
            return new Field(data[field.field]).isEmpty()
        default:
            return
    }
}

const validation = (data, arrayValidator) => {
    const errors = []
    arrayValidator.forEach(field => {
        let result = identifer(data, field)
        if (!result.correct) errors.push({ id: field.id || '', field: field.field, error: result })
    })
    return errors
}
export default validation