import {
    REGISTER,
    LOGIN,
    LOGOUT,
    DATA_REQUESTED,
    DATA_FAILURE,
    UPDATE_CURRENT_DATA
} from "../actions/types";

const INITIAL_STATE = {
    isLoading: false,
    user: {
        _id: '',
        firstName: '',
        lastName: '',
        username: '',
        email: '',
        createdAt: '',
        updatedAt: ''
    },
    token: null,
    error: {}
}

export default (state = { ...INITIAL_STATE }, action) => {
    switch (action.type) {
        case REGISTER:
            return { ...state, token: action.payload.token }
        case LOGIN:
            return { ...state, ...action.payload }
        case LOGOUT:
            return state = INITIAL_STATE
        case DATA_REQUESTED:
            return { ...state, isLoading: true }
        case UPDATE_CURRENT_DATA:
            return { ...state, isLoading: false, user: { ...action.payload } }
        case DATA_FAILURE:
            return { ...state, isLoading: false, error: action.payload }
        default:
            return state
    }
}