export const defaultOptions = {
    duration: 3500,
    onClick: null,
    closeOnClick: true,
    displayButtonClose: false,
    displayIcon: true,
    icon: 'fas fa-exclamation',
    text: 'Toast alert.',
    theme: 'success'
}
export const themes = {
    success: {
        className: 'success',
        color: '#27ae60'
    },
    warn: {
        className: 'warn',
        color: '#ecb605'
    },
    danger: {
        className: 'danger',
        color: '#d32f13'
    }
}
export const validateOptions = options => {
    if (typeof options.duration !== "number") {
        options.duration = defaultOptions.duration
    }
    if (typeof options.onClick !== "function") {
        options.onClick = defaultOptions.onClick
    }
    if (typeof options.closeOnClick !== "boolean") {
        options.closeOnClick = defaultOptions.closeOnClick
    }
    if (typeof options.icon !== "string") {
        options.icon = defaultOptions.icon
    }
    if (typeof options.text !== "string") {
        options.text = defaultOptions.text
    }
    if (typeof options.theme !== "string" || !themes[options.theme]) {
        options.theme = defaultOptions.theme
    }
    if (typeof options.displayIcon !== "boolean") {
        options.displayIcon = defaultOptions.displayIcon
    }
    return options
}